import { Field, ObjectType } from "@nestjs/graphql";
import { Column, Entity, OneToMany } from "typeorm";
import { BaseEntityColumn } from "../base.entity";
import { PaymentGatewayPlan } from "./payment-gateway-plan.entity";

@ObjectType()
@Entity()
export class PaymentPlan extends BaseEntityColumn {

  @Field(() => String)
  @Column()
  name: string;

  @Field(() => [PaymentGatewayPlan])
  @OneToMany(() => PaymentGatewayPlan, (paymentGatewayPlan) => paymentGatewayPlan.paymentPlan)
  paymentGatewayPlans: PaymentGatewayPlan[];
  
}
